// mixins
@mixin flex() {
  display: -moz-box;
  display: -ms-flexbox;
  display: flex; }

@mixin center() {
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center; }

@mixin column() {
  -moz-box-orient: vertical;
  -moz-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column; }

@mixin skew() {
  -webkit-transform: skew(-5deg, -5deg) rotate(-2.5deg);
  transform: skew(-5deg, -5deg) rotate(-2.5deg);
  -webkit-transform-origin: center center;
  transform-origin: center center; }

@mixin shadow() {
  text-shadow: 1px 1px #d10e31, 2px 2px #d10e31, 3px 3px #d10e31, 4px 4px #d10e31, 5px 5px #d10e31, 6px 6px #d10e31, 7px 7px #d10e31, 8px 8px #890920, 9px 9px #890920, 10px 10px #890920, 11px 11px 15px rgba(0,0,0,0.5); }

@mixin rotate($deg) {
  -webkit-transform: rotate($deg);
  -moz-transform: rotate($deg);
  -o-transform: rotate($deg);
  transform: rotate($deg); }

@mixin transition($s) {
  -webkit-transition: $s ease-in-out;
  -moz-transition: $s ease-in-out;
  -o-transition: $s ease-in-out;
  transition: $s ease-in-out; }

@mixin responsive($xsfs, $smvw, $lgrem) {
  font-size: $xsfs;
  line-height: $xsfs;
  @media (min-width: $sm) {
    font-size: $smvw;
    line-height: $smvw;
    @media (min-width: $lg) {
      font-size: $lgrem;
      line-height: $lgrem; } } }
